<template>
  <div class="my-alert" :class="status" >
      <h4 
        v-if="status == 'success'"
      > 
        {{ msg || successMsg }}
      </h4>
      <h4
        v-else
      >
        {{ msg || errorMsg }}
      </h4>
  </div>
</template>

<script>
export default {
    name : 'myAlert',
    props : [
        'status',
        'msg'
    ],
    data() {
        return {
            successMsg : 'Success',
            errorMsg : 'Something wrong please try again',
        }
    }
}
</script>

